// –°–æ–∑–¥–∞–µ–º —Ñ—É–Ω–∫—Ü–∏—é –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç —Ç–µ–∫—Å—Ç–æ–≤—ã–π —à–∞–±–ª–æ–Ω html –¥–æ–∫—É–º–µ–Ω—Ç–∞,
// –≤ DOM –µ–ª–µ–º–µ–Ω—Ç—ã
export function fromHTML(html, data = {}) {
  for (const key in data) {
    const value = data[key];

    html = html.replace(`{{${key}}}`, value);
  }

  const creator = document.createElement('div');
  creator.innerHTML = html;

  if (!creator.children.length) {
    return null;
  }

  if (creator.children.length === 1) {
    return creator.children[0];
  }

  return [...creator.children];
}
