// –°–æ–∑–¥–∞–µ–º –±–∞–∑–æ–≤—ã–π –∫–ª–∞—Å—Å, –∫–æ—Ç–æ—Ä—ã–π –Ω–∞–º –ø–æ–∑–≤–æ–ª–∏—Ç –∏–º–µ—Ç—å –æ–±—â–µ–µ –ø–æ–≤–µ–¥–µ–Ω–∏–µ,
// –¥–ª—è –≤—Å–µ—Ö –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤
export class BaseComponent {
  constructor(model) {
    this._model = model;
    this.outElement = this._createElements();

    if (!this.outElement) {
      this._error('does not create outElement');
    }

    this._model.onUpdate(() => this.render());
    this._model.onRemove(() => this.outElement.remove());

    this._bindEvents();
    this.render();
  }

  // –†–µ–∞–ª–∏–∑—É–µ–º –º–µ—Ç–æ–¥ –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞,
  // –ø—Ä–∏ –ø–æ–º–æ—â–∏ –∫–æ—Ç–æ—Ä–æ–≥–æ –Ω–∞—à –º–∏–Ω–∏ —Ñ—Ä–µ–π–º–≤–æ—Ä–∫ –±—É–¥–µ—Ç —É–≤–µ–¥–æ–º–ª—è—Ç—å –∫–ª–∞—Å—Å
  // –æ —Ç–æ–º —á—Ç–æ –Ω–µ–æ–±—Ö–æ–∏–¥–º–æ –æ–±–Ω–æ–≤–∏—Ç—å –¥–∞–Ω–Ω—ã–µ –≤ –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–µ
  render() {
    this._error('does not implement the render() method');
  }

  _initLifeCycle() {

  }

  // –†–µ–∞–ª–∏–∑—É–µ–º –º–µ—Ç–æ–¥ –∫–æ—Ç–æ—Ä—ã–π –¥–æ–ª–∂–µ–Ω —Å–æ–∑–¥–∞–≤–∞—Ç—å –≤—Å–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ
  // —ç–ª–µ–º–µ–Ω—Ç—ã –¥–ª—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  _createElements() {
    return this._error('has no method for creating DOM elements');
  }

  _bindEvents() {}

  get _className() {
    // –ü–æ–ª—É—á–∞–µ–º –∏–º—è –∫–ª–∞—Å—Å–∞ –∫–æ—Ç–æ—Ä—ã–π –Ω–∞—Å–ª–µ–¥—É–µ—Ç –±–∞–∑–æ–≤—ã–π –∫–ª–∞—Å—Å
    // class SomeComponent extends BaseComponent {}
    // this.constructor.name === 'SomeComponent'
    return this.constructor.name;
  }

  _error(message) {
    // –í—ã–±—Ä–∞—Å—ã–≤–∞–µ–º –æ—à–∏–±–∫—É, —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –æ —Ç–æ–º —á—Ç–æ –Ω–∞—à –¥–æ—á–µ–Ω–∏–π –∫–ª–∞—Å—Å
    // –Ω–µ —Ä–µ–∞–ª–∏–∑—É–µ—Ç –º–µ—Ç–æ–¥ –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
    throw new Error(`Component ${this._className}: ${message}`);
  }
}
